.friendMessage {
    display: flex;
    flex-direction: column;
    align-items: start;
    align-self: start;
    margin-bottom: 45px;
}

.friendImageAndText {
    display: flex;
    gap: 15px;
    align-items: end;
    margin-bottom: 5px;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    object-fit: cover;
}

.friendText {
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
    width: 100%;
    max-width: 550px;
    min-width: 150px;
    border-radius: 15px 15px 15px 0;
    min-height: 60px;
    padding: 5px 15px;
    background-color: #F5F7FB;
    filter: drop-shadow(0px 1px 2px rgba(29, 33, 38, 0.10)) drop-shadow(0px 5px 20px rgba(29, 33, 38, 0.03));
}

.friendText:after {
    content: '';
    position: absolute;
    z-index: -9999;
    left: -20px;
    bottom: 0;
    border: 30px solid transparent;
    border-bottom: 20px solid #F5F7FB;
    border-right: 10px solid #F5F7FB;
}

.friendName {
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.friendMessageText {
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    word-break:break-all;
    white-space: pre-wrap;
}

.friendTime {
    width: 48px;
    color: #000;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    padding-left: 10px;
    margin: 0;
}
