.sendForm {
    margin: 75px;
    display: flex;
    gap: 40px;

}

.textarea {
    width: 980px;
    min-height: 40px;

    border-radius: 20px;
    border: none;
    background: #F5F7FB;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.10), 0 5px 20px 0 rgba(29, 33, 38, 0.03);

    overflow: auto;
    outline: none;

    resize: none;
    padding: 12px 0 0 25px;

    &::placeholder {
        color: rgba(0, 0, 0, 0.60);
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
}

.button {
    width: 120px;
    height: 40px;
    border-radius: 20px;
    border: none;
    background: #06C;

    color: #FFF;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.button:hover {
    cursor: pointer;
    box-shadow: 0 3px 3px 1px rgba(29, 33, 38, 0.10), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
    transform: scale(1.05);
    transition: 0.2s;
}
.button:active {
    box-shadow: 0 3px 3px 1px rgba(255, 0, 73, 0.41), 0 5px 20px 0 rgba(4, 49, 110, 0.37);
}
