.message {
    display: flex;
    flex-direction: column;
    align-self: end;
    align-items: end;
    margin-bottom: 45px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    gap: 15px;
    align-items: end;
    margin-bottom: 5px;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    object-fit: cover;
}

.text {
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: space-between;
    align-items: end;
    width: 100%;
    max-width: 550px;
    min-width: 150px;
    border-radius: 15px 15px 0 15px;
    min-height: 60px;
    padding: 5px 15px;
    background-color: #06C;
    filter: drop-shadow(0px 1px 2px rgba(29, 33, 38, 0.10)) drop-shadow(0px 5px 20px rgba(29, 33, 38, 0.03));
}

.text:after {
    content: '';
    position: absolute;
    z-index: -9999;
    right: -20px;
    bottom: 0;
    border: 30px solid transparent;
    border-bottom: 20px solid #06C;
    border-left: 10px solid #06C;
}

.name {
    color: #FFF;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.messageText {
    color: #FFF;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    word-break:break-all;
    white-space: pre-wrap;
}

.time {
    width: 48px;
    text-align: end;
    color: #000;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    padding-right: 12px;
    margin: 0;
}